body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #007bff;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo img {
  width: auto;
  height: 40px;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  margin: 0 15px;
}

nav li:first-child {
  margin-left: 0;
}

nav li:last-child {
  margin-right: 0;
}

nav a {
  color: var(--blue-color);
  text-transform: uppercase;
  text-align: center;
  padding: 15px 20px;
  font-weight: 500;
  line-height: 50px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

nav a:hover {
  color: #00008b;
}

nav a.is-active {
  color: var(--blue);
}
