body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #007bff;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
}

.logo img {
  height: 40px;
  width: auto;
}

nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav li {
  margin: 0 15px;
}

nav li:first-child {
  margin-left: 0;
}

nav li:last-child {
  margin-right: 0;
}

nav a {
  padding: 15px 20px;
  color: var(--blue-color);
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 50px; /* Высота ссылок */
  transition: color 0.3s;
  position: relative;
  display: inline-block;
  text-align: center;
}

nav a:hover {
  color: darkblue;
}

nav a.is-active {
  color: var(--blue);
}
